import React, { Component } from 'react';
import BankSwitch from './bank-switch';

class BankSelector extends Component {
    render() {
        return (
            <div className="controlpanel__bankselector">
            	<h2 className="controlpanel__bankselector--heading">
            		Kit Selector
            	</h2>
            	<BankSwitch bank="BankA" kit="Casio MT-40" changeBank={this.props.changeBank} currentBank={this.props.currentBank} />
            	<BankSwitch bank="BankB" kit="Pearl SY-1" changeBank={this.props.changeBank} currentBank={this.props.currentBank} />
            </div>
        );
    }
}

export default BankSelector;
