import React, { Component } from 'react';

class DrumPad extends Component {
    render() {
        return (
            <div className="drum-pad drummachine__pads--pad" id={this.props.padid} onClick={this.props.playSampleClick}>
        		<audio src={this.props.sample} className="clip" id={this.props.letter} preload="auto" />
        		<span className="drummachine__pads--pad-text">{this.props.letter}</span>
            </div>
        );
    }
}

export default DrumPad;