import React, { Component } from 'react';
import DrumPad from './drum-pad';
import mt40Kick1 from './audio/mt40/kick1.wav'
import mt40Kick2 from './audio/mt40/kick2.wav'
import mt40Kick3 from './audio/mt40/kick3.wav'
import mt40Snare1 from './audio/mt40/snare1.wav'
import mt40Snare2 from './audio/mt40/snare2.wav'
import mt40Snare3 from './audio/mt40/snare3.wav'
import mt40HatClosed from './audio/mt40/hat-closed.wav'
import mt40HatOpen1 from './audio/mt40/hat-open1.wav'
import mt40HatOpen2 from './audio/mt40/hat-open2.wav'
import sy1Kick1 from './audio/sy1/kick1.wav'
import sy1Kick2 from './audio/sy1/kick2.wav'
import sy1Kick3 from './audio/sy1/kick3.wav'
import sy1Snare1 from './audio/sy1/snare1.wav'
import sy1Snare2 from './audio/sy1/snare2.wav'
import sy1Snare3 from './audio/sy1/snare3.wav'
import sy1HatClosed from './audio/sy1/hat-closed.wav'
import sy1HatOpen1 from './audio/sy1/hat-open1.wav'
import sy1HatOpen2 from './audio/sy1/hat-open2.wav'

class DrumPads extends Component {
    render() {
    	// Q, W, E, A, S, D, Z, X, C
    	var kick1, kick2, kick3, snare1, snare2, snare3, closed, open1, open2;
    	if(this.props.currentBank === 'BankA'){
    		kick1 = mt40Kick1;
    		kick2 = mt40Kick2;
    		kick3 = mt40Kick3;
    		snare1 = mt40Snare1;
    		snare2 = mt40Snare2;
    		snare3 = mt40Snare3;
    		closed = mt40HatClosed;
    		open1 = mt40HatOpen1;
    		open2 = mt40HatOpen2;
    	}else{
    		kick1 = sy1Kick1;
    		kick2 = sy1Kick2;
    		kick3 = sy1Kick3;
    		snare1 = sy1Snare1;
    		snare2 = sy1Snare2;
    		snare3 = sy1Snare3;
    		closed = sy1HatClosed;
    		open1 = sy1HatOpen1;
    		open2 = sy1HatOpen2;
    	}
        return (
        	<div className="drummachine__pads">
            	<DrumPad letter="Q" padid="Kick-1" sample={kick1} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="W" padid="Snare-1" sample={snare1} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="E" padid="Open-Hat" sample={open2} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="A" padid="Kick-2" sample={kick2} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="S" padid="Snare-2" sample={snare2} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="D" padid="Closed-Hat-2" sample={open1} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="Z" padid="Kick-3" sample={kick3} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="X" padid="Snare-3" sample={snare3} playSampleClick={this.props.playSampleClick} />
	            <DrumPad letter="C" padid="Closed-Hat-1" sample={closed} playSampleClick={this.props.playSampleClick} />
	        </div>
        );
    }
}

export default DrumPads;
