import React, { Component } from 'react';

class Display extends Component {
    render() {
        return (
            <div className="controlpanel__display">
            	<div className="controlpanel__display--text">
            		<span className="controlpanel__display--text-kit" id="display-current-kit">{this.props.currentKit}</span>
            		<span className="controlpanel__display--text-sample" id="display">{this.props.currentSample}</span>
            	</div>
            </div>
        );
    }
}

export default Display;