import React, { Component } from 'react';
import Display from './display';
import BankSelector from './bank-selector';

class ControlPanel extends Component {
    render() {
        return (
            <div className="controlpanel">
            	<h1 className="controlpanel__heading">
            		Smack Box
            	</h1>
            	<Display currentSample={this.props.currentSample} currentBank={this.props.currentBank} currentKit={this.props.currentKit} />
            	<BankSelector changeBank={this.props.changeBank} currentBank={this.props.currentBank} />
            </div>
        );
    }
}

export default ControlPanel;
