import React, { Component } from 'react';

class BankSwitch extends Component {
    render() {
    	var ledStyle;
    	if(this.props.currentBank === this.props.bank){
			ledStyle = {
				backgroundColor: '#dc275c',
				boxShadow: '0 0 2rem #dc275c'
			}
		}else{
			ledStyle = {
				backgroundColor: 'rgba(0, 0, 0, .5)'
			}
		}
        return (
            <div className="controlpanel__bankselector--switch">
            	<span className="controlpanel__bankselector--switch-text">{this.props.kit}</span>
            	<button className="controlpanel__bankselector--switch-button" id={this.props.bank} onClick={this.props.changeBank}></button>
            	<span className="controlpanel__bankselector--switch-led" style={ledStyle} id={this.props.bank} />
            </div>
        );
    }

}


export default BankSwitch;
