import React, { Component } from 'react';
import DrumPads from './drum-pads';
import ControlPanel from './control-panel';

class DrumMachine extends Component {
    constructor(props) {
        super(props);
        this.state = {
        	bank: 'BankA',
        	kit: 'Casio MT-40'
        }
        this.changeBank = this.changeBank.bind(this);
    }

    changeBank(e){
    	this.setState({ bank: e.target.id })
    	switch(e.target.id){
    		case 'BankA':
    			this.setState({ kit: 'Casio MT-40'});
    			break;
    		case 'BankB':
    			this.setState({ kit: 'Pearl SY-1'});
    			break;
    		default:
    			break;
    	}
    }

    render() {
        return (
        	<div className="drummachine">
            	<DrumPads play={this.props.play} currentBank={this.state.bank} playSampleClick={this.props.playSampleClick} />
            	<ControlPanel changeBank={this.changeBank} currentBank={this.state.bank} currentSample={this.props.currentSample} currentKit={this.state.kit} />
            </div>
        );
    }
}

export default DrumMachine;
