import React, { Component } from 'react';
import DrumMachine from './components/drum-machine';
import AppFooter from './components/app-footer';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      currentSample: 'Hit a pad!'
    }
    this.playSampleKey = this.playSampleKey.bind(this);
    this.playSampleClick = this.playSampleClick.bind(this);
  }

  playSampleKey(e){
    var audio = document.getElementById(e.key.toUpperCase());
    if(audio){
      try{ 
        audio.currentTime = 0;
        audio.play();
      }catch(err){
        console.log(err);
      }
      var pad = audio.parentElement;
      var str = pad.id;
      str = str.replace(/-/g, ' ');
      this.setState({ currentSample: str });
      pad.classList.add('drummachine__pads--pad-isplaying');
      setTimeout(()=>{
        pad.classList.remove('drummachine__pads--pad-isplaying');
      }, 100)
    }
  }

  playSampleClick(e){
      var audio = e.target.children[0];
      var pad = document.getElementById(e.target.id);
      if(!audio){
        audio = document.getElementById(e.target.textContent);
        pad = audio.parentElement;
      }
      if(audio){
        try{
        audio.currentTime = 0;
        audio.play();
      }catch(err){
        console.log(err);
      }
        var str = pad.id;
        str = str.replace(/-/g, ' ');
        this.setState({ currentSample: str });
        pad.classList.add('drummachine__pads--pad-isplaying');
        setTimeout(()=>{
          pad.classList.remove('drummachine__pads--pad-isplaying');
        }, 100)
      }
    }
 
  componentDidMount(){
    document.addEventListener("keydown", this.playSampleKey, false);
  }
  componentWillUnmount(){
    document.removeEventListener("keydown", this.playSampleKey, false);
  }

  render() {
    return (
      <div className="app">
        <div id="drum-machine" className="app__content">
          <DrumMachine currentSample={this.state.currentSample} playSampleClick={this.playSampleClick} />
          <AppFooter />
        </div>
      </div>
    );
  }
}

export default App;
